	module.exports = {"data":{"post":{"html":"<p>Mama didn't like the Aqua theme, so I've switched to Basic Blog. I set the font to 12 points... I hope that works for everyone, including Grandma!</p>\n<hr />\n<font color=\"maroon\">Okay, now that our home page doesn't resemble the front of our kitchen stove anymore I can contribute something!</p>\n<p>I've kept busy keeping Christopher from pulling <a href=\"http://shanson.editthispage.com/pictures/viewer$317\">cables</a>. Yes, any electrical cords that we've tried to hide he'll go after.<br />\n<a href=\"http://w3.one.net/~voyager/candl.html\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/08/cl.jpg\" height=\"123\" width=\"118\" align=\"left\" alt=\"c&l: \" border=\"0\" /></a> I've re-discovered my favorite TV series <a href=\"http://w3.one.net/~voyager/candl.html\">Cagney and Lacey</a>. The re-runs are on every afternoon and are a highlight in my daily routine.</font></p>","fields":{"title":"Laid-up Layout","date":"05 Aug 2000","url":"https://papascott.de/2000-08-05-laid-up-layout/"}}},"pathContext":{"slug":"/2000-08-05-laid-up-layout/","prev":{"fields":{"title":"PhotoPoint","date":"04 Aug 2000","slug":"/2000-08-04-photopoint/"}},"next":{"fields":{"title":"CyClassics","date":"06 Aug 2000","slug":"/2000-08-06-cyclassics/"}}}}

